.nav {
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: 1024px) {
    flex-direction: row;
  }
}

.closed + .nav {
  display: none;

  @media (min-width: 1024px) {
    display: flex;
  }
}

.nav__item {
  margin-top: 20px;

  @media (min-width: 1024px) {
    margin-top: 0;
  }
}

.nav__link {
  color: $nav-link-color;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 16px;
  opacity: 0.6;

  @media (min-width: 1024px) {
    font-size: 16px;
    margin-left: 20px;
  }

  &--active {
    color: $nav-link-color-active;
  }
}

.nav__arrow {
  position: fixed;
  width: 40px;
  bottom: 2%;
  right: 2%;

  @media (min-width: 768px) {
    width: 60px;
  }

  @media (min-width: 1024px) {
    width: 80px;
  }
}